.modal-product {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: none;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);

  &__container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 30px 15px;
    border-radius: 10px;
    padding: 40px 30px;
    width: 700px;
    background-color: var(--color-white);

    @include xxs {
      top: 5%;
    }
  }

  &__close {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
    user-select: none;
  }
}
