.header {
  &__container {
    display: flex;
    min-height: 768px;
    max-width: 1400px;

    @include xxs {
      min-height: 100vh;
    }
  }

  &__nav {
    position: absolute;
    left: 16.6%;
    top: 75px;
    width: 1030px;
    max-width: 100%;

    @include md {
      left: 5%;
      top: 75px;
      width: 80vw;
      max-width: 800px;
    }
  }
}

.main {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: calc(100% - 274px);

  &__container {
    position: relative;
    width: 1127px;
    height: 100%;
    max-width: 100%;

    @include sm {
      padding: 0;
      width: 768px;
    }
  }

  &__text-img {
    position: absolute;
    left: 16%;
    bottom: 8.3%;
    display: block;
    width: 100%;
    height: 100%;
    max-width: 486px;
    max-height: 200px;
    background-image: url("../img/headerTitle.png");
    background-position: top left;
    background-size: contain;
    background-repeat: no-repeat;

    @include sm {
      left: 16%;
      max-width: 370px;
      max-height: 150px;
    }

    @include xs {
      left: 8%;
      width: 300px;
    }

    @include xxs {
      left: 2%;
      width: 300px;
    }
  }

  @include md {
    max-width: 100%;
  }
}

.player {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1000;
  margin: 0 auto;
  width: 80%;
  min-height: 40%;
  transform: translate(-50%, -50%);

  &__wrap {
    border-radius: 5px;
    padding: 5px;
    width: 100%;
    min-height: 100%;
    background-color: var(--color-mystic);
  }

  &__close {
    display: block;
    margin-left: auto;
    border: none;
    border-radius: 0;
    width: 20px;
    height: 20px;
    background-color: none;
    cursor: pointer;
  }

  &__video {
    z-index: 2;
    width: inherit;
    background: var(--color-black);
  }

  &__btn {
    display: inline-block;
    border-radius: 19px;
    padding: 6px 23px;
    min-width: 87px;
    min-height: 19px;
    font-family: "NunitoSans";
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
    color: var(--color-white);
    background: var(--color-tan);
  }

  &__video-track {
    margin-bottom: 10px;
    width: auto;
    height: 5px;
    background-color: var(--color-fiord);
  }

  &__timeline {
    display: inline-block;
    width: 0;
    height: 5px;
    background-color: var(--color-tan);
  }

  &__buttons {
    padding: 5px 0;
  }

  &__controls {
    display: inline-block;
    width: auto;
  }
}

.block-video {
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  min-height: 768px;
  max-width: 690px;
  background-color: var(--color-black);

  &__wrapper {
    position: relative;
    width: 100%;
    height: 100%;
  }

  &__png {
    display: flex;
    align-items: center;
    justify-content: center;
    height: inherit;
    background-image: url("../img/header.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @include sm {
      width: 100%;
    }
  }

  &__play-button {
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 54px;
    height: 54px;
    background-color: var(--color-white);
    cursor: pointer;

    > span {
      display: inline-block;
      width: 10px;
      height: 11px;
      background-image: url("../img/Triangle.png");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  @include sm {
    width: inherit;
    height: inherit;
    min-height: 100%;
    max-width: 100%;
  }
}

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 216px 25px 130px 56px;
  min-width: 274px;
  color: var(--color-white);
  background-color: var(--color-fiord);

  &__title {
    margin: 0 0 22px;
    padding: 14px 40px 14px 0;
    font-family: "NunitoSans";
    font-weight: 700;
    font-size: 16px;
    user-select: none;
  }

  &__list {
    overflow: hidden;
    padding-bottom: 20px;
    font-size: 16px;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 29px;
    }
  }

  &__link {
    position: relative;
    font-family: "NunitoSans";
    font-weight: 300;
    color: var(--color-mystic);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -3px;
      width: 100%;
      height: 2px;
      background-color: var(--color-tan);
      opacity: 0;
      transition: opacity 0.15s;
    }

    &:hover {
      &::before {
        opacity: 1;
        transition: opacity 0.15s;
      }
    }
  }

  &.open {
    transform: translateX(0);
    transition: all 0.3s;
  }

  @include md {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 102;
    padding-top: 50px;
    width: 80%;
    min-height: 768px;
    max-width: 400px;
    box-shadow: 5px 0 15px -5px rgba(0, 0, 0, 0.8);
    background-color: var(--color-fiord);
    transform: translateX(-150vw);
    transition: all 0.3s;
  }

  @include lg {
    min-height: 100%;
  }
}

.nav {
  z-index: 100;
  display: flex;
  align-items: flex-end;
}

.logo {
  width: 110px;
  height: 37px;

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.cart {
  display: inline-flex;
  margin-bottom: 5px;
  margin-left: auto;
  width: auto;
  height: auto;

  &__icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    background-image: url("../img/cart.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__quantity {
    display: flex;
    align-items: center;
    margin-top: -13px;
    margin-left: -4px;
    border-radius: 25px;
    padding: 0 5px;
    height: 20px;
    font-family: "NunitoSans";
    font-weight: 300;
    font-size: 14px;
    color: var(--color-white);
    background-color: var(--color-tan);
  }
}
