.person-coffe {
  padding: 88px 40px 127px;
  text-align: center;

  @include xs {
    padding: 30px 40px;
    height: 100%;
    min-height: 100vh;
  }
}

.coffe-blocks {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: start;

  &__wrap-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 25px;
    width: 114px;
    height: 114px;
  }

  &__icon {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &_1 {
      width: 80px;
      height: 81px;
      background-image: url("../img/icon_1.png");
    }

    &_2 {
      width: 79px;
      height: 79px;
      background-image: url("../img/icon_2.png");
    }

    &_3 {
      width: 80px;
      height: 81px;
      background-image: url("../img/icon_3.png");
    }

    &_4 {
      width: 75px;
      height: 81px;
      background-image: url("../img/icon_4.png");
    }
  }

  &__desc {
    margin: 0 0 15px;
    font-family: "NunitoSans";
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--color-ebonyClay);
  }

  &__text {
    margin: 0;
    font-family: "NunitoSans";
    font-weight: 300;
    font-size: 16px;
    line-height: 21px;
    color: var(--color-mirage);
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 12px;
    min-height: 247px;
    max-width: 247px;

    &:not(:last-child) {
      margin-right: 47px;

      @include lg {
        margin-right: 0;
      }

      @include xs {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }

    &:nth-child(-n+2) {
      @include lg {
        margin-bottom: 30px;
      }
    }
  }

  @include lg {
    justify-content: space-between;
    width: 543px;
  }

  @include xs {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 493px;
    max-width: 100%;
  }
}