@import url(./vendor/swiper-bundle.min.css);
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-zircon: #f9fbff;
  --color-fiord: #415167;
  --color-tan: #c7a17a;
  --color-mirage: #151d28;
  --color-spanishWhite: #f9f5e8;
  --color-mystic: #edf0f5;
  --color-ebonyClay: #232c38;
  --color-mirage: #151d28;
  --color-alto: #dadada;
  --color-concrete: #f2f2f2;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "NunitoSans";
  src: url("../fonts/NunitoSans-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "NunitoSans";
  src: url("../fonts/NunitoSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "NunitoSans";
  src: url("../fonts/NunitoSans-LightItalic.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "NunitoSans";
  src: url("../fonts/NunitoSans-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "NunitoSans";
  src: url("../fonts/NunitoSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

