html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-weight: 900;
  font-size: 16px;
  background-color: var(--color-zircon);
}

@media (max-width: 375px) {
  body {
    font-size: 12px;
  }
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
  user-select: none;
}

ul {
  list-style: none;
}

.site-container {
  overflow: hidden;
  margin: 0 auto;
  max-width: 1400px;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.title {
  margin: 0 auto 5px;
  max-width: 330px;
  font-family: "NunitoSans";
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: var(--color-fiord);
}

@media (max-width: 992px) {
  .title {
    font-size: 19px;
  }
}

@media (max-width: 375px) {
  .title {
    font-size: 16px;
  }
}

.main-title {
  margin: 0 auto 73px;
  max-width: 448px;
  font-size: 30px;
  line-height: 34px;
  text-transform: uppercase;
  color: var(--color-fiord);
}

@media (max-width: 992px) {
  .main-title {
    font-size: 24px;
  }
}

@media (max-width: 630px) {
  .main-title {
    margin-bottom: 30px;
  }
}

@media (max-width: 375px) {
  .main-title {
    font-size: 22px;
  }
}

.btn_1 {
  display: inline-block;
  border-radius: 19px;
  padding: 6px 23px;
  min-height: 19px;
  max-width: 87px;
  font-family: "NunitoSans";
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: var(--color-white);
  background: var(--color-tan);
}

.btn_2 {
  display: inline-block;
  vertical-align: sub;
  border-radius: 19px;
  padding: 6px 2px;
  max-width: 67px;
  max-height: 19px;
  font-family: "NunitoSans";
  font-weight: 700;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: var(--color-fiord);
  background: transparent;
}

.btn-load {
  display: none;
  border-radius: 19px;
  padding: 6px 23px;
  width: 160px;
  height: 33px;
  font-family: "NunitoSans";
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: var(--color-white);
  background: var(--color-tan);
}

@media (max-width: 630px) {
  .btn-load {
    display: inline-block;
  }
}

.lock {
  overflow: hidden;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 101;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
}

.overlay.open {
  opacity: 1;
  visibility: visible;
}

.opacity-slide {
  opacity: 0.3;
}

.visible-hidden {
  opacity: 0;
  visibility: hidden;
}

.display-flex {
  display: flex !important;
}

.hidden-block {
  display: none !important;
}

.margin-top-xs {
  margin-top: 0 !important;
}

.margin-top-sm {
  margin-top: 30px !important;
}

.card--combo {
  display: flex;
  flex-direction: column;
  width: 360px;
  min-height: 562px;
  text-align: left;
  background-color: var(--color-white);
}

@media (max-width: 630px) {
  .card--combo {
    width: 235px;
    min-height: 100%;
  }
}

.header__container {
  display: flex;
  min-height: 768px;
  max-width: 1400px;
}

@media (max-width: 375px) {
  .header__container {
    min-height: 100vh;
  }
}

.header__nav {
  position: absolute;
  left: 16.6%;
  top: 75px;
  width: 1030px;
  max-width: 100%;
}

@media (max-width: 992px) {
  .header__nav {
    left: 5%;
    top: 75px;
    width: 80vw;
    max-width: 800px;
  }
}

.main {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: calc(100% - 274px);
}

.main__container {
  position: relative;
  width: 1127px;
  height: 100%;
  max-width: 100%;
}

@media (max-width: 768px) {
  .main__container {
    padding: 0;
    width: 768px;
  }
}

.main__text-img {
  position: absolute;
  left: 16%;
  bottom: 8.3%;
  display: block;
  width: 100%;
  height: 100%;
  max-width: 486px;
  max-height: 200px;
  background-image: url("../img/headerTitle.png");
  background-position: top left;
  background-size: contain;
  background-repeat: no-repeat;
}

@media (max-width: 768px) {
  .main__text-img {
    left: 16%;
    max-width: 370px;
    max-height: 150px;
  }
}

@media (max-width: 630px) {
  .main__text-img {
    left: 8%;
    width: 300px;
  }
}

@media (max-width: 375px) {
  .main__text-img {
    left: 2%;
    width: 300px;
  }
}

@media (max-width: 992px) {
  .main {
    max-width: 100%;
  }
}

.player {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1000;
  margin: 0 auto;
  width: 80%;
  min-height: 40%;
  transform: translate(-50%, -50%);
}

.player__wrap {
  border-radius: 5px;
  padding: 5px;
  width: 100%;
  min-height: 100%;
  background-color: var(--color-mystic);
}

.player__close {
  display: block;
  margin-left: auto;
  border: none;
  border-radius: 0;
  width: 20px;
  height: 20px;
  background-color: none;
  cursor: pointer;
}

.player__video {
  z-index: 2;
  width: inherit;
  background: var(--color-black);
}

.player__btn {
  display: inline-block;
  border-radius: 19px;
  padding: 6px 23px;
  min-width: 87px;
  min-height: 19px;
  font-family: "NunitoSans";
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: var(--color-white);
  background: var(--color-tan);
}

.player__video-track {
  margin-bottom: 10px;
  width: auto;
  height: 5px;
  background-color: var(--color-fiord);
}

.player__timeline {
  display: inline-block;
  width: 0;
  height: 5px;
  background-color: var(--color-tan);
}

.player__buttons {
  padding: 5px 0;
}

.player__controls {
  display: inline-block;
  width: auto;
}

.block-video {
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  min-height: 768px;
  max-width: 690px;
  background-color: var(--color-black);
}

.block-video__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.block-video__png {
  display: flex;
  align-items: center;
  justify-content: center;
  height: inherit;
  background-image: url("../img/header.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 768px) {
  .block-video__png {
    width: 100%;
  }
}

.block-video__play-button {
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 54px;
  height: 54px;
  background-color: var(--color-white);
  cursor: pointer;
}

.block-video__play-button > span {
  display: inline-block;
  width: 10px;
  height: 11px;
  background-image: url("../img/Triangle.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 768px) {
  .block-video {
    width: inherit;
    height: inherit;
    min-height: 100%;
    max-width: 100%;
  }
}

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 216px 25px 130px 56px;
  min-width: 274px;
  color: var(--color-white);
  background-color: var(--color-fiord);
}

.sidebar__title {
  margin: 0 0 22px;
  padding: 14px 40px 14px 0;
  font-family: "NunitoSans";
  font-weight: 700;
  font-size: 16px;
  user-select: none;
}

.sidebar__list {
  overflow: hidden;
  padding-bottom: 20px;
  font-size: 16px;
}

.sidebar__item:not(:last-child) {
  margin-bottom: 29px;
}

.sidebar__link {
  position: relative;
  font-family: "NunitoSans";
  font-weight: 300;
  color: var(--color-mystic);
}

.sidebar__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -3px;
  width: 100%;
  height: 2px;
  background-color: var(--color-tan);
  opacity: 0;
  transition: opacity 0.15s;
}

.sidebar__link:hover::before {
  opacity: 1;
  transition: opacity 0.15s;
}

.sidebar.open {
  transform: translateX(0);
  transition: all 0.3s;
}

@media (max-width: 992px) {
  .sidebar {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 102;
    padding-top: 50px;
    width: 80%;
    min-height: 768px;
    max-width: 400px;
    box-shadow: 5px 0 15px -5px rgba(0, 0, 0, 0.8);
    background-color: var(--color-fiord);
    transform: translateX(-150vw);
    transition: all 0.3s;
  }
}

@media (max-width: 1210px) {
  .sidebar {
    min-height: 100%;
  }
}

.nav {
  z-index: 100;
  display: flex;
  align-items: flex-end;
}

.logo {
  width: 110px;
  height: 37px;
}

.logo img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cart {
  display: inline-flex;
  margin-bottom: 5px;
  margin-left: auto;
  width: auto;
  height: auto;
}

.cart__icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  background-image: url("../img/cart.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.cart__quantity {
  display: flex;
  align-items: center;
  margin-top: -13px;
  margin-left: -4px;
  border-radius: 25px;
  padding: 0 5px;
  height: 20px;
  font-family: "NunitoSans";
  font-weight: 300;
  font-size: 14px;
  color: var(--color-white);
  background-color: var(--color-tan);
}

.burger {
  position: relative;
  display: none;
  align-self: center;
  margin-left: 20px;
  border: none;
  width: 40px;
  height: 22px;
  background-color: transparent;
  cursor: pointer;
}

@media (max-width: 992px) {
  .burger {
    display: block;
  }
}

.burger::before,
.burger::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-black);
}

.burger::after {
  top: auto;
  bottom: 0;
}

.burger span {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  display: inline-block;
  width: 100%;
  height: 2px;
  background-color: var(--color-black);
  transform: translateY(-50%);
}

.lock {
  overflow: hidden;
}

.person-coffe {
  padding: 88px 40px 127px;
  text-align: center;
}

@media (max-width: 630px) {
  .person-coffe {
    padding: 30px 40px;
    height: 100%;
    min-height: 100vh;
  }
}

.coffe-blocks {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: start;
}

.coffe-blocks__wrap-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 25px;
  width: 114px;
  height: 114px;
}

.coffe-blocks__icon {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.coffe-blocks__icon_1 {
  width: 80px;
  height: 81px;
  background-image: url("../img/icon_1.png");
}

.coffe-blocks__icon_2 {
  width: 79px;
  height: 79px;
  background-image: url("../img/icon_2.png");
}

.coffe-blocks__icon_3 {
  width: 80px;
  height: 81px;
  background-image: url("../img/icon_3.png");
}

.coffe-blocks__icon_4 {
  width: 75px;
  height: 81px;
  background-image: url("../img/icon_4.png");
}

.coffe-blocks__desc {
  margin: 0 0 15px;
  font-family: "NunitoSans";
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: var(--color-ebonyClay);
}

.coffe-blocks__text {
  margin: 0;
  font-family: "NunitoSans";
  font-weight: 300;
  font-size: 16px;
  line-height: 21px;
  color: var(--color-mirage);
}

.coffe-blocks__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 12px;
  min-height: 247px;
  max-width: 247px;
}

.coffe-blocks__block:not(:last-child) {
  margin-right: 47px;
}

@media (max-width: 1210px) {
  .coffe-blocks__block:not(:last-child) {
    margin-right: 0;
  }
}

@media (max-width: 630px) {
  .coffe-blocks__block:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
  }
}

@media (max-width: 1210px) {
  .coffe-blocks__block:nth-child(-n+2) {
    margin-bottom: 30px;
  }
}

@media (max-width: 1210px) {
  .coffe-blocks {
    justify-content: space-between;
    width: 543px;
  }
}

@media (max-width: 630px) {
