.title {
  margin: 0 auto 5px;
  max-width: 330px;
  font-family: "NunitoSans";
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: var(--color-fiord);

  @include md {
    font-size: 19px;
  }

  @include xxs {
    font-size: 16px;
  }
}

.main-title {
  margin: 0 auto 73px;
  max-width: 448px;
  font-size: 30px;
  line-height: 34px;
  text-transform: uppercase;
  color: var(--color-fiord);

  @include md {
    font-size: 24px;
  }

  @include xs {
    margin-bottom: 30px;
  }

  @include xxs {
    font-size: 22px;
  }
}

.btn_1 {
  display: inline-block;
  border-radius: 19px;
  padding: 6px 23px;
  min-height: 19px;
  max-width: 87px;
  font-family: "NunitoSans";
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: var(--color-white);
  background: var(--color-tan);
}

.btn_2 {
  display: inline-block;
  vertical-align: sub;
  border-radius: 19px;
  padding: 6px 2px;
  max-width: 67px;
  max-height: 19px;
  font-family: "NunitoSans";
  font-weight: 700;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: var(--color-fiord);
  background: transparent;
}

.btn-load {
  display: none;
  border-radius: 19px;
  padding: 6px 23px;
  width: 160px;
  height: 33px;
  font-family: "NunitoSans";
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: var(--color-white);
  background: var(--color-tan);

  @include xs {
    display: inline-block;
  }
}

.lock {
  overflow: hidden;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 101;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;

  &.open {
    opacity: 1;
    visibility: visible;
  }
}

.opacity-slide {
  opacity: 0.3;
}

.visible-hidden {
  opacity: 0;
  visibility: hidden;
}

.display-flex {
  display: flex !important;
}

.hidden-block {
  display: none !important;
}

.margin-top-xs {
  margin-top: 0 !important;
}

.margin-top-sm {
  margin-top: 30px !important;
}

.card--combo {
  display: flex;
  flex-direction: column;
  width: 360px;
  min-height: 562px;
  text-align: left;
  background-color: var(--color-white);

  @include xs {
    width: 235px;
    min-height: 100%;
  }
}