html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-weight: 900;
  font-size: 16px;
  background-color: var(--color-zircon);

  @include xxs {
    font-size: 12px;
  }
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
  user-select: none;
}

ul {
  list-style: none;
}

.site-container {
  overflow: hidden;
  margin: 0 auto;
  max-width: 1400px;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}
