.favorite {
  position: relative;
  padding: 50px 0 129px;
  text-align: center;
  background-color: var(--color-concrete);

  &__background {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 0;
    margin-right: 40px;
    width: 384px;
    height: 100%;
    background-position: 0 100%;
    background-size: cover;
    background-color: var(--color-fiord);

    @include lg {
      width: 300px;
    }

    @include md {
      width: 250px;
    }

    @include sm {
      width: 0;
    }
  }

  &__main-title {
    margin-bottom: 70px;

    @include xs {
      margin-bottom: 30px;
    }
  }

  @include xs {
    padding: 30px 5px;
    min-height: 100vh;
  }
}

.card {
  display: flex;
  justify-content: start;
  overflow: hidden;
  width: 559px;
  height: 100%;
  background-color: var(--color-white);

  &__wrap-price {
    display: block;
    margin: 0 0 7px;
    width: 100%;
  }

  &__discount {
    display: inline-block;
    min-width: 0;
    font-family: "NunitoSans";
    font-weight: 300;
    font-size: 14px;
    text-decoration: line-through;
  }

  &__desc {
    display: block;
    padding: 43px 41px 40px;
    min-width: 0;
    text-align: left;

    @include xs {
      padding: 10px;
    }
  }

  &__price {
    display: inline-block;
    margin: 0 0 9px;
    font-family: "NunitoSans";
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: var(--color-tan);
  }

  &__title {
    margin: 0 0 22px;
    font-family: "NunitoSans";
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--color-ebonyClay);

    @include xs {
      margin-bottom: 10px;
    }

    @include xxs {
      font-size: 18px;
    }
  }

  &__text {
    display: block;
    margin: 0 0 36px;
    font-family: "NunitoSans";
    font-weight: 300;
    font-size: 16px;

    @include xs {
      margin-bottom: 15px;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include xxs {
      width: 30%;
      height: 30%;
    }
  }

  &__block-btns {
    display: inline-block;

    @include xs {
      display: flex;
      flex-flow: wrap column;
      align-items: center;
    }
  }

  &__btn_1 {
    margin-right: 23px;

    @include xs {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  &__img {
    @include xs {
      width: 100%;
      height: 200px;
    }
  }

  &:nth-child(even) {
    margin-bottom: 30px;
  }

  @include xs {
    flex-direction: column;
    width: 200px;
    min-height: 400px;
  }
}