.giftset {
  &__container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 87px 40px 120px;
    text-align: center;

    @include xs {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }

  &__background {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    width: 1073px;
    background-color: var(--color-mystic);
  }

  &__title {
    margin: 0 auto 7px;
    padding-right: 7px;
  }

  &__main-title {
    margin: 0 auto 63px;
    padding-right: 10px;

    @include xs {
      margin-bottom: 30px;
    }
  }

  &__wrapper-card {
    position: relative;
    display: flex;
    justify-content: flex-end;
    margin: 0 auto;
    width: 1136px;
    height: auto;
    min-height: 463px;
    max-width: 100%;
  }
}