.combo {
  position: relative;
  padding: 92px 0 147px;
  min-height: 906px;
  text-align: center;

  &__background {
    position: absolute;
    right: 0;
    top: 0;
    z-index: -100;
    width: 326px;
    height: 100%;
    background-color: var(--color-fiord);

    @include lg {
      width: 300px;
    }

    @include md {
      width: 250px;
    }

    @include sm {
      width: 0;
    }
  }

  &__main-title {
    margin-bottom: 65px;
    padding-right: 11px;
  }

  @include xs {
    padding: 30px 0 0;
    min-height: 100vh;
  }
}

.small {
  display: block;
  padding: 20px;
  text-align: center;
}

.info {
  font-family: "NunitoSans";
  font-weight: 300;
  color: var(--color-fiord);
}