@mixin lg {
	@media (max-width: (1210px)) {
		@content;
	}
}

@mixin md {
	@media (max-width: (992px)) {
		@content;
	}
}

@mixin sm {
	@media (max-width: (768px)) {
		@content;
	}
}

@mixin xs {
	@media (max-width: (630px)) {
		@content;
	}
}

@mixin xxs {
	@media (max-width: (375px)) {
		@content;
	}
}
