.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: none;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);

  &__container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 30px 15px;
    border-radius: 10px;
    padding: 40px 30px;
    width: 400px;
    min-height: 400px;
    background-color: var(--color-white);

    @include xxs {
      top: 5%;
    }
  }

  &__close {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
    user-select: none;
  }

  &__title {
    margin-bottom: 20px;
    font-family: "NunitoSans";
    font-weight: 700;
    font-size: 30px;
    line-height: 28px;
    text-align: center;
    color: var(--color-fiord);
  }

  &__form {
    display: inline-block;
    text-align: center;

    input {
      display: inherit;
      border: 2px solid var(--color-tan);
      border-radius: 15px;
      padding: 10px;
      height: 42px;
      min-width: 264px;
      font-family: "NunitoSans";
      font-weight: 400;
      font-size: 16px;

      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &:focus {
        outline: none;
        box-shadow: 0 0 2px 2px var(--color-tan);
        color: var(--color-fiord);
      }
    }
  }

  &__btn {
    margin: 0 auto;
    padding: 8px 23px;
    width: 100%;
    min-width: 250px;

    &:focus {
      outline: none;
      box-shadow: 0 0 2px 2px var(--color-tan);
      color: var(--color-fiord);
    }
  }
}