.swiperCombo-container {
  .swiper-slide {
    width: auto;
    height: calc((100% - 30px) / 2);
  }

  .swiper-wrapper {
    height: 542px;
  }

  .swiper-button-next,
  .swiper-button-prev {
    border-radius: 100%;
    width: 48px;
    height: 48px;
    background-position: center;
    background-size: 24px 17px;
    background-repeat: no-repeat;
    background-color: var(--color-tan);

    @include xs {
      opacity: 0;
      visibility: hidden;
    }
  }

  .swiper-button-next {
    right: 4.7%;
    top: 52%;
    background-image: url("../img/arrowRight.svg");
  }

  .swiper-button-prev {
    left: 4.7%;
    top: 52%;
    background-image: url("../img/arrowLeft.svg");
  }

  .swiper-button-next::after,
  .swiper-button-prev::after {
    display: none;
  }
}