.card-content {
  &__img {
    position: absolute;
    left: -0.4%;
    bottom: 0.9%;
    max-width: 464px;
    max-height: 409px;
    object-fit: cover;

    @include lg {
      left: 2%;
      bottom: 10%;
      max-width: 35%;
    }

    @include md {
      left: initial;
      right: 20%;
      top: initial;
      bottom: 7%;
      min-width: 200px;
      max-width: 25%;
    }

    @include sm {
      position: absolute;
      left: 22%;
      top: 17%;
      bottom: initial;
      min-width: 45%;
      max-width: 45%;
      transform: translate(10%, 0);
    }

    @include xs {
      display: none;
    }
  }

  &__desc {
    padding: 65px 156px 64px 297px;
    width: 883px;
    min-height: 100%;
    max-width: 100%;
    text-align: left;
    background-color: var(--color-white);

    @include md {
      padding: 60px 100px 64px 20px;
      width: auto;
    }

    @include sm {
      padding: 55% 20px 20px;
    }

    @include xs {
      padding-top: 100px;
    }
  }

  &__price {
    display: block;
    margin: 0 0 7px;
    font-family: "NunitoSans";
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: var(--color-tan);

    @include xs {
      font-size: 16px;
    }
  }

  &__caption {
    margin: 0 0 15px;
    font-family: "NunitoSans";
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--color-ebonyClay);

    @include xs {
      font-size: 16px;
    }
  }

  &__text {
    margin: 0 0 12px;
    font-family: "NunitoSans";
    font-weight: 300;
    line-height: 1.4;
    color: var(--color-mirage);
  }

  &__block-buttons {
    position: absolute;
    right: 0;
    top: 0;
    display: inline-flex;
    flex-direction: column;
    height: 100%;

    @include sm {
      position: absolute;
      left: 0;
      top: 0;
      flex-direction: row;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--color-mystic);
    width: 88px;
    height: calc(100% / 3);
    font-size: 30px;
    line-height: 34px;
    color: var(--color-fiord);
    background-color: var(--color-mystic);
    opacity: 0.3;
    cursor: pointer;

    @include sm {
      width: calc(100% / 3);
      height: 80px;
    }
  }

  &__btn_1 {
    display: inline-flex;
    flex-shrink: 0;
    justify-content: center;
    margin-right: 68px;
    padding: 10px;
    width: 168px;
    max-width: 168px;
    max-height: 38px;

    @include md {
      margin-right: 0;
    }

    @include xxs {
      margin-bottom: 15px;
    }
  }

  &__btn_2 {
    display: inline-flex;
    flex-shrink: 0;
    justify-content: center;
    vertical-align: middle;
    padding: 10px;
    width: 168px;
    max-width: 168px;
    max-height: 38px;
  }

  &__block-meta {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 34px;

    @include md {
      max-width: 61%;
    }

    @include sm {
      max-width: 100%;
    }
  }

  &__block-btns {
    @include md {
      max-width: 61%;
    }

    @include sm {
      max-width: 100%;
    }

    @include xs {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  @include xs {
    min-height: auto;
  }
}

.btn-active {
  background-color: var(--color-white);
  opacity: 1;
  cursor: initial;
}

.meta {
  position: relative;
  padding: 13px 10px 10px 53px;
  max-width: 100%;

  &__img {
    position: absolute;
    left: 0;
    top: 10px;
    width: 40px;
    height: 40px;
    max-width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &_coffe {
      background-image: url("../img/IconCoffe.png");
    }

    &_mountains {
      background-image: url("../img/iconMountains.png");
    }
  }

  &__text {
    display: block;
    margin-bottom: 4px;
    font-family: "NunitoSans";
    font-weight: 300;
    opacity: 0.8;
  }

  &__subtext {
    display: block;
    font-family: "NunitoSans";
    font-weight: 600;
    opacity: 0.8;
  }

  &:not(:last-child) {
    margin-right: 59px;

    @include md {
      margin-right: 20px;
    }
  }
}
