.burger {
  position: relative;
  display: none;
  align-self: center;
  margin-left: 20px;
  border: none;
  width: 40px;
  height: 22px;
  background-color: transparent;
  cursor: pointer;

  @include md {
    display: block;
  }
}

.burger::before,
.burger::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-black);
}

.burger::after {
  top: auto;
  bottom: 0;
}

.burger span {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  display: inline-block;
  width: 100%;
  height: 2px;
  background-color: var(--color-black);
  transform: translateY(-50%);
}

.lock {
  overflow: hidden;
}
